const $ = document.querySelector.bind(document);
const $$ = document.querySelectorAll.bind(document);

const yesBtn = $(".button-yes");
const noBtn = $(".button-no");
const btnSend = $(".button-send");
const mistakeBox = $(".mistake");
const modal = $(".modal");
const inputBox = $("#window-input");
const windowWarning = $(".window-warning");
const windowText = $(".window-text");
const thankyouBox = $(".thankyou");
const privateMessage = $(".private-message");

// Bật nhạc thui hihi
const music = $(".music");
document.addEventListener("click", (e) => {
  music.play();
});
// Button event listeners
yesBtn.addEventListener("click", (e) => {
  modal.classList.remove("disabled");
});

noBtn.addEventListener("click", (e) => {
  noBtn.classList.add("disabled");
  mistakeBox.innerText = "Em bấm mạnh quá mất nút không rùi :)))";
});

btnSend.addEventListener("click", (e) => {
  if (inputBox.value) {
    windowWarning.innerText = "";
    windowText.classList.add("disabled");
    btnSend.classList.add("disabled");
    inputBox.classList.add("disabled");
    thankyouBox.classList.remove("disabled");
    privateMessage.textContent = inputBox.value;
  } else {
    windowWarning.innerText = "Chưa nhập mà alo :((";
  }
});
